<template>
	<section>
		<div class="inr-banner"><img :src="imgUrlBanner" alt=""></div>
    
	    <div class="inner">
	    	<div class="inf-ssjg">搜索到包含“{{title}}”的<em>{{this.totalNum}}</em>条信息</div>
	    </div>
	       
	    <div class="inner">
		    <ul class="inf-search-titl clearfix" v-if="dataArr.length != 0">
		         <li v-for="(item,index) in dataArr" @click="tabData(item.channelName,index)"><a href="javascript:;"  :class="index == num ? 'cur' : ''">{{item.channelName}}（{{item.total}}）</a></li>
		    </ul>
		    <div class="inf-search-cont"  v-if="dataArr.length != 0">
		        <ul class="inf-search-con">
		           <li v-for="item in listData"  @click="goweb(item.uuid)"><a href="javascript:;">{{item.title}}</a></li>
		        </ul>
		        <div class="c-page">
		            <el-pagination
		            	:current-page.sync='currentpage'
		       		  @current-change="pageFn"
					  layout="prev, pager, next"
					  :total=total>
					</el-pagination>
		        </div>
		    </div>
		    <div class="searchtitle" v-if="dataArr.length == 0">
		    	未搜索到相关数据
		    </div>
	    </div>
	</section>
</template>

<script>
	import {queryCarousels,helpData,listData2} from '../../api/info';
	export default{
		data(){
			return{
				imgUrlBanner:'',
				dataArr:[],
				title:'',
				totalNum:0,
				pageNum:1,
				pageSize:10,
				listData:[],
				total:0,
				channelpara:'',
				currentpage:1,
				num:0
			}
		},
		methods:{
			goweb(param){
				var uuid = window.localStorage.setItem('uuid',param)
// 			 	this.$router.push({
// 			 		path:'/active?uuid='+param,
// 			 	})
				window.open(configURL.infoUrl + '/page/#/active?uuid=' + param,'_blank')
			},
			tabData(para,index){
				this.num = index;
				this.currentpage=1;
				this.channelpara = para;
				this.pageNum =1;
				var parameterData ={
					channelMDesc:'ZX',
					 "channelName": para,
					 "pageNum": 1,
					 "pageSize":10,
					 'searchText':this.title
				}
				listData2(parameterData).then((res)=>{
					this.listData=res.datas.infoList;
					this.total = res.total
				});
			},
			pageFn(sty){
				this.pageNum = sty;
				var parameterData ={
					channelMDesc:'ZX',
					 "channelName": this.channelpara,
					 "pageNum": this.pageNum,
					 "pageSize":10,
					 'searchText':this.title
				}
				listData2(parameterData).then((res)=>{
					
					this.listData=res.datas.infoList;
					this.total = res.total
				});
			},
			initData(){
				var word = this.$route.query.id;
				this.title = word;
				var param = {
					
					searchText:word,
					pageNum:1,
					pageSize:1,
					channelMDesc:'ZX'
				}
				helpData(param).then(res => {
					console.log(res)
					var num = 0;
					var data = [];
					res.datas.forEach(o => {
						if(o.total != 0){
							data.push(o);
							num+=o.total;
						}
					})
					
					this.dataArr = data 
					this.totalNum = num
					
					var paras = data[0].channelName;
					this.channelpara = paras;
					var parameterData ={
						channelMDesc:'ZX',
						 "channelName": paras,
						 "pageNum": this.pageNum,
						 "pageSize":10,
						 'searchText':word
					}
					listData2(parameterData).then((res)=>{
						
						this.listData=res.datas.infoList;
						this.total = res.total
					});
				})
				
				
			},
			getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					
					this.imgUrlBanner = res.datas[0].imageUrl
					
				});
			}
		},
		mounted(){
			this.getBanner()
			this.initData()
		},
		created(){
			
		}
	}
</script>

<style>
	.searchtitle{
		width: 100%;
		height: 200px;
		text-align: center;
		line-height: 200px;
		font-size: 20px;
	}
</style>


